import data, {dataItem} from "../../Data/data";
import {Dispatch} from "redux";

import {appActions} from "../../App/app-reducer";

import { InferValueTypes } from "../../App/store";

const initialState = {
    services: [] as Array<dataItem>,
}

const productListReducer = (state: productListStateType = initialState, action: ActionsType): productListStateType => {
    switch (action.type) {
        case "PRODUCT-LIST/SET-DATA": {
            return {...state, services: [...action.payload]}
        }
        default:
            return state
    }
}

//ACTIONS
export const productListActions = {
    setData: (payload: Array<dataItem>) => ({type: 'PRODUCT-LIST/SET-DATA', payload} as const),
}

//THUNKS
export const productListThunks = {
    getProductData: () => (dispatch: Dispatch) => {
        dispatch(appActions.setStatusLoading(true))
        setTimeout(()=>{
            dispatch(productListActions.setData(data.services))
            dispatch(appActions.setStatusLoading(false))
        }, 3000)
    },
}

//TYPES
type ActionsType = ReturnType<InferValueTypes<typeof productListActions>>
export type productListStateType = typeof initialState

export default productListReducer
