import React, {useEffect} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {Redirect, Route, Switch} from 'react-router-dom';

/* BLL */
import {productListThunks} from "../Features/ProductList/productList-reducer";

/* Types */
import {AppRootStateType} from "./store";
import {dataItem} from "../Data/data";

/* Style */
import style from './App.module.scss';

/* Components */
import Header from "../Features/Header/header";
import FullCard from "../Features/FullCard/fullcard";
import HomePage from "../Features/HomePage/homePage";

function App() {

    const dispatch = useDispatch()
    const products = useSelector<AppRootStateType, Array<dataItem>>(state => state.productList.services)

    useEffect(() => {
        dispatch(productListThunks.getProductData())
    }, [dispatch])

    return (
        <>
            <section className={`${style.section__outer} ${style.bgc__primary}`}>
                <header className={`${style.section__inner} ${style.row}`}>
                    <Header/>
                </header>
            </section>

            <Switch>
                <Route exact path={'/'} render={() => (
                    <section className={`${style.section__outer}`}>
                        <div className={`${style.section__inner}`}>
                            <HomePage/>
                        </div>
                    </section>
                )}/>

                {
                    products.map(product => <Route key={product.alias} path={`/${product.alias}`} render={() => (

                        <section className={`${style.section__outer}`}>
                            <div className={`${style.section__inner}`}>
                                <FullCard {...product}/>
                            </div>
                        </section>
                    )}/>)
                }

                <Route path={'/404'} render={() => <h1>404</h1>}/>
                <Redirect from={'*'} to={'/404'}/>
            </Switch>
        </>
    )
}

export default App;