import React, {FC} from 'react';
import {useSelector} from "react-redux";

/* Types */
import {filterType} from "../Filters/filter-reducer";
import {AppRootStateType} from "../../App/store";
import {dataItem} from "../../Data/data";

/* Components */
import Card from "../Card/card";

/* Utils */
import filterUtil from "../../utils/filterUtil";

const ProductList: FC = () => {

    const items = useSelector<AppRootStateType, Array<dataItem>>(state => state.productList.services)
    const filters = useSelector<AppRootStateType, Array<filterType>>(state => state.filters.activeFilter)

    return <>
        {
            filterUtil(items, filters).map(item => <Card {...item} key={item.alias}/>)
        }
    </>
}
export default ProductList
